const fetchURL =
	'https://raw.githubusercontent.com/vizbyarcher/provaccin/master/data/vaccinari.json'
const getItems = () => fetch(fetchURL).then(res => res.json())

const propertyFirst = 'county'
const propertySecond = 'city'
const propertyThird = 'name'
const propertyFourth = 'date'
const propertyFifth = 'available'
const propertySixth = 'scheduled'
const propertySeventh = 'address'
const propertyEighth = 'id'
const propertyNinth = 'product'
const propertyTenth = 'history'
const propertyEleventh = 'capacity'

// data.sort((a, b) => a.city.localeCompare(b.city) || b.price - a.price);
const changeFirst = data => {
	if (data) {
		// console.log(data)
		const unique = [
			...new Set(
				data
					.sort((a, b) => b[propertyFirst] - a[propertyFirst])
					.map(item => item[propertyFirst])
			),
		]
		// console.log(unique)
		return unique.sort()
	} else {
		// console.log('No data for First combo...')
		return []
	}
}

const changeSecond = (data, valueFirst) => {
	if (valueFirst) {
		// console.log('Changing Second combo...')
		const filteredFirst = data.filter(
			item => item[propertyFirst] === valueFirst
		)
		const unique = [...new Set(filteredFirst.map(item => item[propertySecond]))]
		// console.log(unique)
		return unique.sort()
	} else {
		return []
	}
}

const changeThird = (data, valueFirst, valueSecond) => {
	if (valueFirst) {
		// console.log('Changing Third combo...')
		const filteredFirst = data.filter(
			item => item[propertyFirst] === valueFirst
		)
		if (valueSecond) {
			const filteredSecond = filteredFirst.filter(
				item => item[propertySecond] === valueSecond
			)
			const unique = [
				...new Set(filteredSecond.map(item => item[propertyThird])),
			]
			// console.log('There is second value')
			return unique.sort()
		} else {
			const unique = [
				...new Set(filteredFirst.map(item => item[propertyThird])),
			]
			// console.log(unique)
			// console.log(unique.sort())
			return unique.sort()
		}
	} else {
		return []
	}
}

const ComboBoxItems = () => {
	// const handleOnChange = ({ target }) => console.log(target.value)
	const [valueFirst, setValueFirst] = useState('Bucuresti')
	const [valueSecond, setValueSecond] = useState(null)
	const [valueThird, setValueThird] = useState(null)
	const [items, setItems] = useState([])
	const [itemsFirst, setItemsFirst] = useState([])
	const [itemsSecond, setItemsSecond] = useState([])
	const [itemsThird, setItemsThird] = useState([])

	const [updated, setUpdated] = useState(null)
	// data.sort((a, b) => b[propertyFifth] - a[propertyFifth])
	// data.sort((a, b) => a.city.localeCompare(b.city) || b.price - a.price);
	useEffect(() => {
		getItems().then(data => {
			// data.sort(
			// 	(a, b) =>
			// 		b[propertyFifth] - a[propertyFifth] ||
			// 		a[propertySixth] - b[propertySixth]
			// )
			const criteria = [
				{ key: propertyFifth, order: 'desc', type: 'numeric' },
				{ key: propertySixth, order: 'asc', type: 'numeric' },
				{ key: propertySecond, order: 'asc', type: 'string' },
				{ key: propertyThird, order: 'asc', type: 'string' },
			]
			setItems(sortValues(data, criteria))
			setUpdated(data.find(x => x.id === 0)[propertyFourth])
		})
	}, [])

	useEffect(() => {
		if (items) {
			setItemsFirst(changeFirst(items))
		} else {
			console.log('No data for itemsFirst')
		}
	}, [items])

	useEffect(() => {
		if (valueFirst) {
			setItemsSecond(changeSecond(items, valueFirst))
		} else {
			setItemsSecond([])
		}
	}, [items, valueFirst])

	useEffect(() => {
		if (items.length > 0) {
			if (valueFirst) {
				setItemsThird(changeThird(items, valueFirst, valueSecond))
				if (!valueSecond && valueThird) {
					setValueSecond(
						items.find(x => x[propertyThird] === valueThird)[propertySecond]
					)
				}
			} else {
				setItemsThird([])
			}
		}
	}, [items, valueFirst, valueSecond, valueThird])

	const DenseTable = props => {
		const classes = useStyles()
		const items = props.props.items
		const valueFirst = props.props.valueFirst
		const valueSecond = props.props.valueSecond
		const valueThird = props.props.valueThird

		const getProduct = (value, classes) => {
			if (value.charAt(0) === 'P') {
				if (value === 'P-B') {
					return classes.rowPP
				} else {
					return classes.rowP
				}
			} else if (value.charAt(0) === 'M') {
				if (value === 'M') {
					return classes.rowMM
				} else {
					return classes.rowM
				}
			} else if (value.charAt(0) === 'A') {
				if (value === 'A') {
					return classes.rowAA
				} else {
					return classes.rowA
				}
			} else if (value.charAt(0) === 'J') {
				if (value === 'J') {
					return classes.rowJJ
				} else {
					return classes.rowJ
				}
			} else {
				return classes.rowNull
			}
		}

		const getCompletion = (history, scheduled) => {
			if (history === '?#?') {
				return '?'
			} else {
				const speeds = history
					.split('#')[0]
					.split(',')
					.map(str => parseInt(str.match(/\d+/g)))
				// console.log(Math.ceil(scheduled/Math.max(...speeds)))
				return Math.ceil(scheduled / Math.max(...speeds))
			}
		}

		let filtered = items
		if (valueThird) {
			filtered = items
				.filter(item => item[propertyFirst] === valueFirst)
				.filter(item => item[propertySecond] === valueSecond)
				.filter(item => item[propertyThird] === valueThird)
		} else if (valueSecond) {
			filtered = items
				.filter(item => item[propertyFirst] === valueFirst)
				.filter(item => item[propertySecond] === valueSecond)
		} else if (valueFirst) {
			filtered = items.filter(item => item[propertyFirst] === valueFirst)
		}

		return (
			<TableContainer component={Paper}>
				{filtered && (
					<div>
						<small>
							<i>{filtered.length} inregistrari</i>
						</small>
					</div>
				)}
				<Table
					stickyHeader
					className={classes.table}
					size='small'
					aria-label='a dense table'>
					<TableHead>
						<TableRow>
							<TableCell className={classes.tableCell}>Centrul</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Libere
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Lista asteptare
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Capacitate vaccinare
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Epuizare lista(zile)
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Vaccin(uri) administrat(e)
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Viteza vaccinare/zi
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Ultima publicare
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Localitatea
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Judetul
							</TableCell>
							<TableCell className={classes.tableCell} align='right'>
								Adresa
							</TableCell>
							{/* <TableCell align='right'>Actualizat&nbsp;la</TableCell> */}
						</TableRow>
					</TableHead>
					<TableBody>
						{filtered.map(item => (
							<TableRow key={item[propertyEighth]}>
								<TableCell
									component='th'
									scope='row'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertyThird].replace(/_/g, ' ')}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertyFifth]}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertySixth]}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertyEleventh]}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{getCompletion(item[propertyTenth], item[propertySixth])}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertyNinth]
										.replace(/A/, 'AstraZeneca')
										.replace(/J/, 'Johnson')
										.replace(/M/, 'Moderna')
										.replace(/P-B/, 'Pfizer')
										.replace(/\//g, ', ')}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertyTenth]
										.split('#')[0]
										.replace(/,/g, ' ')
										.replace(/: /g, ':')
										.replace(/&Johnson/g, '')
										.replace(/ - BIONTech/g, '')
										.replace(/-/g, '')}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertyTenth]
										.split('#')[1]
										.replace(/,/g, ' ')
										.replace(/: /g, ':')
										.replace(/ - BIONTech/g, '')
										.replace(/&Johnson/g, '')
										.replace(/a-Z/g, 'aZ')}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertySecond]}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertyFirst]}
								</TableCell>
								<TableCell
									align='right'
									className={getProduct(item[propertyNinth], classes)}>
									{item[propertySeventh]}
								</TableCell>
								{/* <TableCell align='right'>{item[propertyFourth]}</TableCell> */}
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
		)
	}

	return (
		<div className='combo-county'>
			{items && (
				<Autocomplete
					id='combo-box-first'
					options={itemsFirst}
					getOptionLabel={option => option}
					value={valueFirst}
					style={{ width: 380 }}
					onChange={(event, value) => {
						// console.log(`first: ${value}`)
						if (value) {
							// console.log('changing first value', value)
							setValueFirst(value)
							setValueSecond(null)
							setValueThird(null)
						} else {
							// console.log('no first value...')
							setValueFirst(null)
							setValueSecond(null)
							setValueThird(null)
						}
					}}
					renderInput={params => (
						<TextField
							{...params}
							label='Judetul:'
							variant='outlined'
							fullWidth
						/>
					)}
				/>
			)}
			<br></br>
			{items && (
				<Autocomplete
					id='combo-box-second'
					options={itemsSecond}
					getOptionLabel={option => option}
					value={valueSecond}
					style={{ width: 380 }}
					onChange={(event, value) => {
						// console.log(`second: ${value}`)
						if (value) {
							// console.log('changing second value', value)
							setValueSecond(value)
							setValueThird(null)
						} else {
							// console.log('no second value...')
							setValueSecond(null)
							setValueThird(null)
						}
					}}
					renderInput={params => (
						<TextField
							{...params}
							label='Localitatea/sectorul:'
							variant='outlined'
							fullWidth
						/>
					)}
				/>
			)}
			<br></br>
			{items && (
				<Autocomplete
					id='combo-box-second'
					options={itemsThird}
					getOptionLabel={option => option}
					value={valueThird}
					style={{ width: 380 }}
					onChange={(event, value) => {
						// console.log(`third: ${value}`)
						if (value) {
							// console.log('changing third value', value)
							setValueThird(value)
						} else {
							// console.log('no third value...')
							setValueThird(null)
						}
					}}
					renderInput={params => (
						<TextField
							{...params}
							label='Centrul:'
							variant='outlined'
							fullWidth
						/>
					)}
				/>
			)}
			{/* {valueFirst && <div>{`first: ${valueFirst}`}</div>} */}
			{/* {valueSecond && <div>{`second: ${valueSecond}`}</div>} */}
			{/* {valueThird && <div>{`third: ${valueThird}`}</div>} */}
			{updated && (
				<p>
					<small>Date actualizate la: {updated}</small>
				</p>
			)}
			<p>
				<small>
					<i>
						Legenda - Fiecare centru e colorat dupa tipul de vaccin administrat
						(exclusiv/majoritar):
					</i>
				</small>
				<br />
				<small>
					<span style={{ color: '#46b31f' }}> Pfizer </span>
				</small>
				/
				<small>
					<span style={{ color: '#009144' }}> Pfizer </span>
				</small>
				,
				<small>
					<span style={{ color: '#008da9' }}> Moderna </span>
				</small>
				/
				<small>
					<span style={{ color: '#006bbb' }}> Moderna </span>
				</small>
				,
				<small>
					<span style={{ color: '#ac1983' }}> AstraZeneca </span>
				</small>
				/
				<small>
					<span style={{ color: '#7d45a8' }}> AstraZeneca </span>
				</small>
				,
				<small>
					<span style={{ color: '#f65a14' }}> Johnson </span>
				</small>
				/
				<small>
					<span style={{ color: '#ff9d00' }}> Johnson </span>
				</small>
				,
				<small>
					<span style={{ color: '#212121' }}> Nespecificat </span>
				</small>
			</p>

			{updated && (
				<DenseTable props={{ items, valueFirst, valueSecond, valueThird }} />
			)}
		</div>
	)
}

const MainPage = () => {
	//  https://stackoverflow.com/questions/36878255/allow-access-control-allow-origin-header-using-html5-fetch-api
	// console.log(data)
	return (
		<>
			<ThemeProvider theme={theme}>
				<CssBaseline>
					<Header />
					<Container maxWidth='sm'>
						<Box my={4}>
							<Typography
								variant='h4'
								component='h1'
								gutterBottom
								color='textSecondary'
								style={{ fontWeight: 600 }}>
								PROgramare VACCIN COVID-19
							</Typography>
							<Body />
							<ComboBoxItems />
							<Copyright />
						</Box>
					</Container>
				</CssBaseline>
			</ThemeProvider>
		</>
	)
}

export default function App() {
	const routeResult = useRoutes(routes)
	return routeResult || <MainPage />
}
