// Function for dynamic sorting based on multiple properties
/*  try this code in https://jsfiddle.net/ */

const sortValues = (object, criteria) => {
	const formatDate = (stringDate, sep = '-', reverse = true) => {
		// converting '31-01-2020' to ISO standard form '2020-01-31'
		if (!stringDate.includes(sep)) {
			/* console.log('Date format does not contains selected separator...') */
			return stringDate
		} else {
			const parts = stringDate.split(sep)
			if (reverse) {
				// reversing date string order
				return `${parts[2]}-${parts[1]}-${parts[0]}`
			} else {
				return `${parts[0]}-${parts[1]}-${parts[2]}`
			}
		}
	}

	const compareValues = (key, order = 'asc', type = 'numeric') => {
		return function (a, b) {
			switch (type) {
				case 'numeric':
					let numericA = 0
					let numericB = 0
					if (!a.hasOwnProperty(key)) {
						// if property doesn't exist on object set arbitrary value for sorting
						numericA = order.toLowerCase().includes('desc')
							? -Infinity
							: Infinity
					} else {
						numericA = a[key]
					}
					if (!b.hasOwnProperty(key)) {
						// if property doesn't exist on object set arbitrary value for sorting
						numericB = order.toLowerCase().includes('desc')
							? -Infinity
							: Infinity
					} else {
						numericB = b[key]
					}
					let comparisonNumeric = 0
					if (numericA > numericB) {
						comparisonNumeric = 1
					} else if (numericA < numericB) {
						comparisonNumeric = -1
					}
					return order.toLowerCase().includes('desc')
						? comparisonNumeric * -1
						: comparisonNumeric
				case 'string':
					let stringA = ''
					let stringB = ''
					if (!a.hasOwnProperty(key)) {
						// if property doesn't exist on object set arbitrary value for sorting
						stringA = order.toLowerCase().includes('desc')
							? ''
							: `${String.fromCharCode(65535)}${String.fromCharCode(
									65535
							  )}${String.fromCharCode(65535)}`
					} else {
						stringA = typeof a[key] === 'string' ? a[key].toUpperCase() : a[key]
					}
					if (!b.hasOwnProperty(key)) {
						// if property doesn't exist on object set arbitrary value for sorting
						stringB = order.toLowerCase().includes('desc')
							? ''
							: `${String.fromCharCode(65535)}${String.fromCharCode(
									65535
							  )}${String.fromCharCode(65535)}`
					} else {
						stringB = typeof b[key] === 'string' ? b[key].toUpperCase() : b[key]
					}
					let comparisonString = 0
					comparisonString = stringA.localeCompare(stringB)
					return order.toLowerCase().includes('desc')
						? comparisonString * -1
						: comparisonString
				case 'date':
					let dateA = 0
					let dateB = 0
					if (!a.hasOwnProperty(key)) {
						// property doesn't exist on object
						dateA = order.toLowerCase().includes('desc') ? -Infinity : Infinity
					} else {
						dateA = Date.parse(formatDate(a[key]))
					}
					if (!b.hasOwnProperty(key)) {
						// property doesn't exist on object
						dateB = order.toLowerCase().includes('desc') ? -Infinity : Infinity
					} else {
						dateB = Date.parse(formatDate(b[key]))
					}
					let comparisonDate = 0
					if (dateA > dateB) {
						comparisonDate = 1
					} else if (dateA < dateB) {
						comparisonDate = -1
					}
					return order.toLowerCase().includes('desc')
						? comparisonDate * -1
						: comparisonDate
				default:
					// console.log('Unsortable type selected...')
					return 0
			}
		}
	}

	for (var c in criteria.reverse()) {
		/* console.log(criteria[c].key, criteria[c].order, criteria[c].numeric) */
		object.sort(
			compareValues(criteria[c].key, criteria[c].order, criteria[c].type)
		)
		/* console.log(object) */
	}
	return object
}

export default sortValues

