// install (please make sure versions match peerDependencies)
// yarn add @nivo/core @nivo/stream
import { ResponsiveStream } from '@nivo/stream'
// make sure parent container have a defined height when using
// responsive component, otherwise height will be 0 and
// no chart will be rendered.
// website examples showcase many properties,
// you'll often use just a few of them.
import styled from 'styled-components'

const Wrapper = styled.div`
	height: 400px;
	/* width: 600px; */
`
const Container = styled.div`
	max-width: 600px;
	width: 99%;
	margin: 0 auto;
`
const StreamChart = ({ data }) => (
	<Container>
		<h4>Chart Title</h4>
		<Wrapper>
			<ResponsiveStream
				data={data}
				// animate={false}
				keys={['Raoul', 'Josiane', 'Marcel', 'René', 'Paul', 'Jacques']}
				margin={{ top: 10, right: 15, bottom: 50, left: 40 }}
				axisTop={null}
				axisRight={null}
				axisBottom={{
					orient: 'bottom',
					tickSize: 5,
					tickPadding: 5,
					tickRotation: 0,
					legend: '',
					legendOffset: 36,
				}}
				axisLeft={{
					orient: 'left',
					tickSize: 5,
					tickPadding: 5,
					tickRotation: 0,
					legend: '',
					legendOffset: -40,
				}}
				offsetType='silhouette'
				colors={{ scheme: 'set1' }}
				fillOpacity={0.85}
				borderColor={{ theme: 'background' }}
				// defs={[
				// 	{
				// 		id: 'dots',
				// 		type: 'patternDots',
				// 		background: 'inherit',
				// 		color: '#2c998f',
				// 		size: 4,
				// 		padding: 2,
				// 		stagger: true,
				// 	},
				// 	{
				// 		id: 'squares',
				// 		type: 'patternSquares',
				// 		background: 'inherit',
				// 		color: '#e4c912',
				// 		size: 6,
				// 		padding: 2,
				// 		stagger: true,
				// 	},
				// ]}
				// fill={[
				// 	{
				// 		match: {
				// 			id: 'Paul',
				// 		},
				// 		id: 'dots',
				// 	},
				// 	{
				// 		match: {
				// 			id: 'Marcel',
				// 		},
				// 		id: 'squares',
				// 	},
				// ]}
				dotSize={8}
				dotColor={{ from: 'color' }}
				dotBorderWidth={2}
				dotBorderColor={{ from: 'color', modifiers: [['darker', 0.7]] }}
				legends={[
					{
						anchor: 'bottom',
						direction: 'row',
						justify: false,
						translateX: 0,
						translateY: 46,
						itemsSpacing: 0,
						itemDirection: 'left-to-right',
						itemWidth: 65,
						itemHeight: 10,
						itemOpacity: 0.75,
						symbolSize: 12,
						symbolShape: 'circle',
						symbolBorderColor: 'rgba(0, 0, 0, .5)',
						effects: [
							{
								on: 'hover',
								style: {
									itemBackground: 'rgba(0, 0, 0, .03)',
									itemOpacity: 1,
								},
							},
						],
					},
				]}
			/>
		</Wrapper>

		<p>Footer</p>
	</Container>
)

export default StreamChart
